.navbar{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 170px;
}
.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 60px;
    font-size: 15px;
    text-decoration: none;
    color: white;
}
.nav-connect{
    padding: 20px 40px;
    border-radius:50px;
    background: linear-gradient(267deg, #e4b2f3 0.36%,#B923E1 102.06%);
    font-size: 22px;
    cursor: pointer;
    transition:0.5s;
    text-decoration: none;
    color: white;
}  
.nav-connect:hover{
    transform: scale(1.05);
}
.nav-menu li{
    display: flex;
    flex-direction: column;
    gap: 5px;
    cursor: pointer;
    text-decoration: none;
    color: white;
}
.nav-menu li img{
    display: flex;
    margin: auto;
}
.anchor-link{
    text-decoration: none;
    color: white;
}
.anchor-link:hover{
    color: white;
    text-decoration: none;
}
.nav-mob-close{
    display: none;
}
.nav-mob-open{
    display: none;
}
@media(max-width:768px){
    .navbar{
        margin: 20px 50x;
    }
    .nav-connect{
        display: none;

    }
    .nav-mob-open{
        display: none;
        position: fixed;
        right: 30px;
    }
    .nav-mob-close{
        display: block;
        position: relative;
        top: 30px;
        left: 290px;
        width: 30px;
    }
    .nav-menu{
         position: fixed;
         flex-direction: column;
         align-items: start;
         top: 0;
         gap: 30px;
         background-color: #1f0016;
         width: 350px;
         height: 100%;
         z-index: 2;
         transition: right 0.5s;
         right: -350px;
    }
    .nav-menu li{
        font-size: 30px;
        padding-left: 100px;
        flex-direction: row;
        gap: 20px;

    }
}
